#app-content>div[data-v-51698ed0] {
	width: 100%;
	height: 100%;
	padding: 20px;
	display: flex;
	flex-direction: column;
	flex-grow: 1;
    max-height: calc(100vh - 50px);
    overflow-y: auto;
}
input[type='text'][data-v-51698ed0] {
	width: 100%;
}
textarea[data-v-51698ed0] {
	flex-grow: 1;
	width: 100%;
}
.file-display[data-v-51698ed0] {
	display: flex;
	align-items: center;
	margin-bottom: 10px;
	border: 1px solid #e0e0e0;
	padding: 10px;
	border-radius: 5px;
}
.thumbnail[data-v-51698ed0] {
	width: 80px;
	/* Width of the thumbnail */
	height: 80px;
	/* Height of the thumbnail */
	background-size: cover;
	background-position: center;
	margin-right: 20px;
	/* Space between thumbnail and details */
	border-radius: 5px;
	flex-shrink: 0;
	/* Prevent thumbnail from shrinking */
}
.file-details[data-v-51698ed0] {
	flex-grow: 1;
	/* Allow details to take up remaining space */
}

/* Responsive adjustments */
@media (max-width: 600px) {
.thumbnail[data-v-51698ed0] {
		width: 50px;
		height: 50px;
		margin-right: 10px;
}
.file-details p[data-v-51698ed0] {
		font-size: 14px;
}
}
.delete-button[data-v-51698ed0] {
	background-color: #ff4b5a;
	color: #fff;
	border: none;
	padding: 5px 10px;
	border-radius: 5px;
	cursor: pointer;
	transition: background-color 0.3s;
	margin-left: 10px;
	/* Space between the details and the button */
}
.delete-button[data-v-51698ed0]:hover {
	background-color: #e43f51;
}

/* Responsive adjustments */
@media (max-width: 600px) {
.delete-button[data-v-51698ed0] {
		padding: 3px 7px;
		font-size: 12px;
}
}
